
import React, { useEffect, useState } from 'react';
import './Detail.scss';
import { useParams } from 'react-router-dom';


function Detail(props){
    let {id} = useParams();
    let [alert, SetAlert] = useState(true);  
    let copyStuck = [...props.stuck];
    let thisStuck = [...props.stuck][id];
    let thisShoes = props.shoesData.find(function(shoes){
        return shoes.id == id;
    }) 
    useEffect(()=>{
        let timer = setTimeout(()=>{SetAlert(false)},2000);
        return ()=>{clearTimeout(timer)}
    },[alert]);

    return (
        <div className="container">
            {
                alert === true
                ? (
                    <div className="my-alert">
                        <p>재고가 얼마남지 않았어요!</p>
                    </div>
                  )
                :null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src={"https://codingapple1.github.io/shop/shoes"+(thisShoes.id+1)+".jpg"} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{thisShoes.title}</h4>
                    <p>{thisShoes.content}</p>
                    <p>{thisShoes.price}</p>
                    <Info stuck = {thisStuck}></Info>
                    <button className="btn btn-danger" onClick={()=> {
                        copyStuck[id]--; 
                        props.SetStuck(copyStuck);
                    }}>주문하기</button>                     
                    
                </div>
            </div>
        </div>  
    )
  };

  function Info(props){
    return(
        <p>재고 :{props.stuck}</p>
    )
  }
  
  export default Detail 